@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.5.0/font/bootstrap-icons.css);
body{background-color:#f8f9fa;min-height:100vh}img{width:100%}.content,#root{height:100%}.btn.btn-secondary{background-color:#141d22}.btn-danger{float:right}.btn-info{color:white}.btn-info:hover{color:white}.theme-dark *{background:rgba(20,29,34,0.84) !important;color:white !important;border-color:#f8f9fa}.theme-dark .card.mb-3{border-color:#f8f9fa}.theme-dark .btn-danger{background-color:#a00000 !important}.theme-dark .col{border-color:#f8f9fa}.theme-dark .card-link.list-link{color:rgba(0,222,255,0.82) !important}.theme-dark .form-control{background:#212e36 !important}.theme-dark .actions .bi-heart-fill.-active{color:red !important}.theme-dark .bi.bi-trash{color:red !important}.theme-dark .auth-actions .nav-link{color:#00deff !important}.theme-dark .navbar-name{color:#00deff !important}.theme-dark .navbar-name .bi-person-circle{color:#00deff !important}


.collection-header .navbar{background-color:#141d22 !important;display:flex;align-items:center;justify-content:center}.collection-header .navbar-brand{margin-left:100px;font-weight:bold;color:#00deff !important;font-size:32px}.collection-header .navbar-name{font-size:20px;padding-top:10px;font-weight:500;cursor:default}.collection-header .navbar-nav{margin-right:100px;align-items:center}.collection-header .navbar-collapse{display:flex;align-items:center;justify-content:flex-end}.collection-header .user-nav{display:flex;list-style:none;margin-top:10px;padding-right:6%;text-decoration:none}.collection-header .btn-outline-success{color:#00deff;border-color:#00deff}.collection-header .btn-outline-success:hover{color:#141d22;background-color:#00deff;border-color:#00deff}.collection-header .nav-link{color:#00deff;cursor:pointer}.collection-header .auth-actions{display:contents}.collection-header .bi.bi-moon{color:white}.collection-header .bi-person-circle{color:#00deff}

.collection-list .col{margin-top:15px;margin-left:5%;margin-right:5%}.collection-list .card-link{text-decoration:none;color:#1f5eaa;margin-right:70%}.collection-list .btn-secondary{margin:20px 5%}.collection-list .btn-link{text-decoration:none;color:#1f5eaa}.collection-list .actions{display:flex;flex-direction:row-reverse;padding-bottom:10px}.collection-list .list-link{flex:auto;padding-left:0}.collection-list .card{box-shadow:0 8px 30px 0 rgba(0,0,0,0.15)}.collection-list .card-author{padding-bottom:10px;display:block}.collection-list .bi-person-circle{color:black}

.registration.container{padding-top:150px;width:600px;margin:0 30%}

.login.container{padding-top:150px;width:600px;margin:0 30%}

.form-collection.container{margin-top:50px;width:35%}

.collection-item-list{margin:20px 20%}.collection-item-list .card-text{text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:4;-webkit-box-orient:vertical;overflow:hidden}.collection-item-list .card-title{overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.collection-item-list .-add{margin-bottom:20px}.collection-item-list .actions{display:flex;margin:5px 15px 5px;flex-direction:row-reverse;padding-bottom:10px}.collection-item-list .btn-link{text-decoration:none;color:#1f69aa}.collection-item-list .bi-person-circle{color:black}.collection-item-list .card-body-list{box-shadow:0 8px 30px 0 rgba(0,0,0,0.15)}.collection-item-list .card-author{flex:auto}

.form-collection{margin:0 30%}

.collection-item-view{margin:40px 20%}.collection-item-view .mb-3-comment{margin-top:20px}.collection-item-view .card-title{overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.collection-item-view .form-label{font-weight:bold}.collection-item-view .actions-comment{margin-top:15px}.collection-item-view .card-body{box-shadow:0 8px 30px 0 rgba(0,0,0,0.15)}.collection-item-view .card-text-date{font-size:12px;color:#5e5e5e}.collection-item-view .bi-person-circle{color:black}.collection-item-view .actions{display:flex;flex-direction:row-reverse}.collection-item-view .actions .bi-heart-fill{flex:auto;color:blue;cursor:pointer}.collection-item-view .actions .bi-heart-fill.-active{color:red}.collection-item-view .actions2 .bi.bi-trash{color:red;cursor:pointer}.collection-item-view .actions2 .bi.bi-gear-fill{text-decoration:none;color:blue}

.collection-comment .mb-3-comment{margin-top:20px}.collection-comment .form-label{font-weight:bold}.collection-comment .actions-comment{margin-top:15px}.collection-comment .card-body-comment{box-shadow:0 8px 30px 0 rgba(0,0,0,0.15);background-color:white;border:1px solid rgba(203,201,201,0.62);margin-top:10px;padding:10px}.collection-comment .card-title{display:flex;flex-direction:row-reverse}.collection-comment .user-name{flex:auto}.collection-comment .trash{padding:1px 2px;border:none}.collection-comment .form-control{box-shadow:0 8px 30px 0 rgba(0,0,0,0.15)}.collection-comment .card-body{box-shadow:0 8px 30px 0 rgba(0,0,0,0.15)}.collection-comment .bi-person-circle{color:black;font-size:17px}

